:root {
  --color-deep-blue: #1a2f4a;
  --color-blue-mid: #2d4a6a;
  --color-teal: #3a7d7c;
  --color-green: #4a9d7e;
  --color-gold: #d4af37;
  --color-gold-light: #f0d98e;
  --color-white: #fff;
  --color-off-white: #f8f9fa;
  --color-gray-light: #e0e4e8;
  --color-gray: #6c757d;
  --color-dark: #212529;
  --font-base: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  --font-size-base: 16px;
  --font-size-lg: 1.25rem;
  --font-size-xl: 1.75rem;
  --font-size-2xl: 2.5rem;
  --font-size-3xl: 3.5rem;
  --spacing-xs: .5rem;
  --spacing-sm: 1rem;
  --spacing-md: 2rem;
  --spacing-lg: 4rem;
  --spacing-xl: 6rem;
  --max-width: 1200px;
  --transition: .3s ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: var(--font-size-base);
}

body {
  font-family: var(--font-base);
  color: var(--color-dark);
  background: linear-gradient(135deg, var(--color-deep-blue) 0%, var(--color-teal) 50%, var(--color-green) 100%);
  min-height: 100vh;
  line-height: 1.6;
}

.container {
  max-width: var(--max-width);
  padding: 0 var(--spacing-md);
  margin: 0 auto;
}

.nav {
  padding: var(--spacing-md) 0;
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.nav-container {
  max-width: var(--max-width);
  padding: 0 var(--spacing-md);
  justify-content: flex-end;
  align-items: center;
  gap: var(--spacing-md);
  margin: 0 auto;
  display: flex;
}

.nav-link {
  color: var(--color-white);
  transition: color var(--transition), opacity var(--transition);
  opacity: .9;
  font-weight: 500;
  text-decoration: none;
}

.nav-link:hover {
  opacity: 1;
  color: var(--color-gold-light);
}

.instagram-icon {
  align-items: center;
  display: flex;
}

.hero {
  text-align: center;
  min-height: 100vh;
  padding: var(--spacing-xl) var(--spacing-md);
  background: linear-gradient(135deg, #1a2f4ab3 0%, #2d4a6ab3 100%), url("hero.2c1df288.png") center / cover no-repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.hero-content {
  max-width: 900px;
  animation: 1s ease-out fadeInUp;
}

.hero-title {
  font-size: var(--font-size-3xl);
  color: var(--color-white);
  margin-bottom: var(--spacing-md);
  letter-spacing: -.02em;
  font-weight: 700;
  line-height: 1.2;
}

.hero-subtitle {
  font-size: var(--font-size-xl);
  color: var(--color-gold-light);
  opacity: .95;
  font-weight: 400;
}

.mission {
  background: var(--color-white);
  padding: var(--spacing-xl) var(--spacing-md);
}

.mission-content {
  max-width: 800px;
  margin: 0 auto;
}

.mission-text {
  font-size: var(--font-size-lg);
  color: var(--color-deep-blue);
  text-align: center;
  line-height: 1.8;
}

.cta {
  background: linear-gradient(135deg, var(--color-teal) 0%, var(--color-green) 100%);
  padding: var(--spacing-xl) var(--spacing-md);
  text-align: center;
}

.cta-title {
  font-size: var(--font-size-2xl);
  color: var(--color-white);
  margin-bottom: var(--spacing-md);
  font-weight: 600;
}

.cta-text {
  font-size: var(--font-size-lg);
  color: var(--color-white);
  margin-bottom: var(--spacing-lg);
  opacity: .95;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta-button {
  padding: var(--spacing-sm) var(--spacing-lg);
  background: var(--color-gold);
  color: var(--color-deep-blue);
  font-size: var(--font-size-lg);
  transition: all var(--transition);
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 4px 15px #0003;
}

.cta-button:hover {
  background: var(--color-gold-light);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #0000004d;
}

.footer {
  background: var(--color-deep-blue);
  padding: var(--spacing-lg) var(--spacing-md);
  text-align: center;
}

.footer-content {
  justify-content: center;
  align-items: center;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-sm);
  flex-wrap: wrap;
  display: flex;
}

.footer-link {
  color: var(--color-gold-light);
  transition: color var(--transition);
  text-decoration: none;
}

.footer-link:hover {
  color: var(--color-gold);
}

.footer-divider {
  color: var(--color-gray);
}

.footer-text {
  color: var(--color-white);
  opacity: .8;
}

.footer-tagline {
  color: var(--color-white);
  opacity: .6;
  font-size: .9rem;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  opacity: 0;
  animation: .8s ease-out forwards fadeInUp;
}

@media (width <= 768px) {
  :root {
    --font-size-3xl: 2rem;
    --font-size-2xl: 1.75rem;
    --font-size-xl: 1.25rem;
    --font-size-lg: 1.1rem;
    --spacing-xl: 3rem;
    --spacing-lg: 2rem;
  }

  .hero {
    padding: var(--spacing-lg) var(--spacing-md);
  }

  .hero-title {
    font-size: var(--font-size-2xl);
  }

  .hero-subtitle {
    font-size: var(--font-size-lg);
  }

  .mission {
    padding: var(--spacing-lg) var(--spacing-md);
  }

  .mission-text {
    font-size: var(--font-size-base);
    line-height: 1.7;
  }

  .cta {
    padding: var(--spacing-lg) var(--spacing-md);
  }

  .cta-title {
    font-size: var(--font-size-xl);
  }

  .cta-text {
    font-size: var(--font-size-base);
  }

  .cta-button {
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: var(--font-size-base);
  }
}

@media (width <= 480px) {
  .container, .nav-container {
    padding: 0 var(--spacing-sm);
  }
}
